import { useState, useEffect, forwardRef } from 'react';
import { motion } from 'framer-motion';
import { Github, Globe2 } from 'lucide-react';
import { useScrollFullpage } from '../hooks/useScrollFullpage';
import { useTranslation } from 'react-i18next'
import { useMediaQuery } from '../hooks/useMediaQuery';
import db from '../static/db/portfolio.json';

function debounce(callback, delay) {
  let timer;
  return function (...args) {
    clearTimeout(timer);
    timer = setTimeout(() => {
      callback.apply(this, args);
    }, delay);
  };
}

const Portfolio = forwardRef((_, rootRef) => {
  const isMobile = useMediaQuery('(max-width: 768px)');
  const [currentPage, setCurrentPage] = useState(0)
  const { ref } = useScrollFullpage();
  const { t } = useTranslation();

  // get current pageIndex
  useEffect(() => {
    const pagesContainer = ref.current;
    const containerPT = parseInt(getComputedStyle(pagesContainer).getPropertyValue("padding-top"))

    const debouncedHandleScroll = debounce(() => {
      const pagesArray = Array.from(pagesContainer.children)
      const pageHeight = pagesArray[0].offsetHeight

      setCurrentPage(Math.floor(
        (pagesContainer.scrollTop + containerPT + (pageHeight / 2)) / pageHeight
      ))
    }, 500)

    pagesContainer.addEventListener('scroll', debouncedHandleScroll);
    return () => pagesContainer.removeEventListener('scroll', debouncedHandleScroll);
  }, [ref]);

  // handle image animation
  useEffect(() => {
    let currentImage = 1
    const imagesArray = Array.from(ref.current.children[currentPage].querySelectorAll('.main-img'))

    const timer = setInterval(() => {
      if (!imagesArray[currentImage]) return

      imagesArray.forEach((item) => item.style.opacity = 0)
      imagesArray[currentImage].style.opacity = 1
      currentImage++

      if (currentImage === imagesArray.length) currentImage = 0
    }, [2000])

    return () => clearInterval(timer)
  }, [ref, currentPage])

  return (
    <div ref={rootRef} className="w-full h-full">
      <div
        ref={ref}
        className={`
          w-full h-full
          rounded-lg bg-white
          overflow-y-auto
          p-[10px]
        `}
      >
        {db.portfolio.map((item, idx) =>
          <div key={idx} className={`
              w-full h-full
              flex flex-col
            `}
          >
            <div className="p-[10px] border-b">
              <span className="text-[28px] text-main">
                {item.name}
              </span>
            </div>
            <div className={`
                h-full 
                flex flex-col 
                md:flex-row
              `}
            >
              <div className={`
                  w-full md:w-[60%] h-full 
                  p-[10px] mt-[10px] mr-[10px] 
                  flex flex-col
                `}
              >
                <div className={`
                    h-[80%] 
                    relative
                  `}
                >
                  {item.images.map((item, idx) =>
                    <img
                      key={idx}
                      src={require('../static/images/' + item.file)}
                      alt="Portfolio"
                      style={{ width: item.size }}
                      className={`
                        w-full h-full
                        absolute
                        top-0 left-[50%] translate-x-[-50%]
                        object-contain
                        transition-all duration-500
                        ${idx !== 0 && 'opacity-0'}
                        main-img
                      `}
                    />
                  )}
                </div>
                <div className={`
                    w-full h-[20%]
                    flex justify-around items-center
                    mt-[20px] md:mt-0
                  `}
                >
                  <motion.button
                    className={`
                      flex items-center gap-[5px] md:gap-[10px]
                      text-[12px]
                      px-[15px]
                      py-[5px] md:py-[7px] 
                      rounded-[25px] 
                      border border-gray-500
                    `}
                    whileHover={{ scale: 1.1, boxShadow: '0 0 8px #60c6db', border: '1px solid #60c6db' }}
                    onClick={() => item.github && window.open(item.github, '_blank')}
                  >
                    <Github />
                    <span>{item.github ? 'Github' : 'Not Available'}</span>
                  </motion.button>
                  <motion.button
                    className={`
                      flex items-center gap-[5px] md:gap-[10px]
                      text-[12px]
                      px-[15px]
                      py-[5px] md:py-[7px] 
                      rounded-[25px] 
                      border border-gray-500
                    `}
                    whileHover={{ scale: 1.1, boxShadow: '0 0 8px #60c6db', border: '1px solid #60c6db' }}
                    onClick={() => item.website && window.open(item.website, '_blank')}
                  >
                    <Globe2 />
                    <span>{item.website ? 'Website' : 'Not Available'}</span>
                  </motion.button>
                </div>
              </div>
              <div className={`
                  w-full md:w-[40%] 
                  pl-[10px] md:pl-[20px]
                  pr-[10px] 
                  flex flex-col 
                  my-[10px] md:border-l
                `}
              >
                <div className="flex-1">
                  <span className="text-[28px] text-main">
                    Introduce
                  </span>
                  <p className="mt-[10px] tracking-[2px] font-light">
                    {t(`portfolio.${item.description}`)}
                  </p>
                </div>
                {!isMobile && (
                  <div className='mt-[20px] md:mt-0 flex-1 shrink-[2]'>
                    <span className="text-[28px] text-main">
                      Badges
                    </span>
                    <div className="flex flex-wrap justify-around mt-[10px]">
                      {item.badges.map((item, idx) =>
                        <img
                          key={idx}
                          src={`https://img.shields.io/badge/${item.label}-gray?style=social&logo=${item.logo}&color=rgb(32%2C%2035%2C%2043)`}
                          alt="tech-badge"
                          className={`
                            h-[20px]
                            md:h-[30px]
                            m-[10px]
                            xl:h-[40px]
                          `}
                        />
                      )}
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  )
})

export default Portfolio;
